import {useDispatch, useSelector} from "react-redux";
import {
    Avatar, Button,
    Container, Divider,
    Grid, ListItem, ListItemAvatar, ListItemText,
    Paper, Typography,
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';

import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {
    updateBalances, updateHedgeFundBalances, updateHedgeFundInfo,
    updateWalletFOOKInfo,
    updateWalletFUDInfo, updateWalletHEDGEInfo,
} from "../redux/slices/coineusSlice";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {useGetPairQuery} from "../redux/services/dexscreener";
import CoineusLoading from "../components/CoineusLoading";

import ArrowCircleUpIcon from '@mui/icons-material/ArrowCircleUp';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import TokenListItem from "../components/TokenListItem";

export default function HomePage() {

    const dispatch = useDispatch();

    const {wallet, marketingWallet, prices, pricesByContractAddress, rpc} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);

    const [info, setInfo] = useState({});
    const [totalD, setTotalD] = useState(undefined);

    const [fundTotal, setFundTotal] = useState(undefined);

    const btc = useGetPairQuery({
        chain_id: 'avalanche',
        pair_address: '0xb28031e312446415bd316c4fff52b99055c28870'
    }, {pollingInterval: 60000})

    useEffect(() => {
        if (btc.data?.pair) setInfo(btc.data.pair);
    }, [btc.data])

    useEffect(() => {
        if (wallet.address) {
            getUserInfo();
        }
    }, [wallet.address]);

    //updates coineus info
    useEffect(() => {

        function getInfo() {
            getHedgeFundInfo();
            getStatsInfo();
        }

        getInfo();
        const interval = setInterval(getInfo, 60000)
        return () => clearInterval(interval);
    }, [])

    useEffect(() => {
        let total = 0;
        Object.entries(marketingWallet.balances.avax).map(([contract_address, balance]) => {
            if(balance && pricesByContractAddress.avax[contract_address]?.inUSD) {
                total +=balance * pricesByContractAddress.avax[contract_address]?.inUSD
            }
        });
        setFundTotal(total);

    }, [marketingWallet, prices])

    const getStatsInfo = () => {
        const web3 = new Web3(rpc.avax);
        const contract = new web3.eth.Contract(coineus_abi, "0xe387cF241B6685D405C575ce35db78ed4844B34a");

        const calls = [
            contract.methods.getTotalDividendsDistributed().call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                td
            ] = resp;

            setTotalD(td / (10 ** 18));
        });

    }

    const getHedgeFundInfo = () => {

        const hedge_fund_market_wallet = "0x6aF557649B0A1c62D7a771Fb308673959d045945";

        //AVAX
        const web3AVAX = new Web3(rpc.avax);
        web3AVAX.eth.getBalance(hedge_fund_market_wallet).then(resp => {
            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: 'native',
                balance: resp / (10 ** 18)
            }))
        })

        const fook_contract = new web3AVAX.eth.Contract(coineus_abi, '0x95923F63dA09B4f7520f7C65a31F318D8228B744');
        const fud_contract = new web3AVAX.eth.Contract(coineus_abi, '0x80363cc2CAa2545554d155031e47049ee2F98f33');
        const usdc_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E');
        const wavax_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7');
        const hedge_contract = new web3AVAX.eth.Contract(coineus_abi, '0xe387cF241B6685D405C575ce35db78ed4844B34a');
        const cheems_contract = new web3AVAX.eth.Contract(coineus_abi, '0x726573a7774317DD108ACcb2720Ac9848581F01D');

        const callsAVAX = [
            fook_contract.methods.balanceOf(hedge_fund_market_wallet).call,
            fook_contract.methods.getAccountDividendsInfo(hedge_fund_market_wallet).call,
            usdc_contract.methods.balanceOf(hedge_fund_market_wallet).call,
            fud_contract.methods.balanceOf(hedge_fund_market_wallet).call,
            fud_contract.methods.getAccountDividendsInfo(hedge_fund_market_wallet).call,
            wavax_contract.methods.balanceOf(hedge_fund_market_wallet).call,
            hedge_contract.methods.balanceOf(hedge_fund_market_wallet).call,
            hedge_contract.methods.getAccountDividendsInfo(hedge_fund_market_wallet).call,
            cheems_contract.methods.balanceOf(hedge_fund_market_wallet).call,
        ];

        makeBatchRequest(web3AVAX, callsAVAX).then(resp => {
            let [
                balance,
                info,
                usdc_balance,
                fud_balance,
                fud_info,
                wavax_balance,
                hedge_balance,
                hedge_info,
                cheems_balance
            ] = resp;

            let withdrawableDividends = info[3];
            let totalDividends = info[4];
            let secondsUntilAutoClaimAvailable = info[7];

            balance = balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 6);
            totalDividends = totalDividends / (10 ** 6);

            dispatch(updateHedgeFundInfo({
                    chain: 'avax',
                    contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744', //fook
                    info: {
                        balance,
                        withdrawableDividends,
                        totalDividends,
                        secondsUntilAutoClaimAvailable
                    }
                }
            ))

            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744', //fook
                balance: balance
            }))

            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E', //usdc
                balance: usdc_balance / (10 ** 6)
            }))

            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7', //wavax
                balance: wavax_balance / (10 ** 18)
            }))

            // FUD TOKEN
            withdrawableDividends = fud_info[3];
            totalDividends = fud_info[4];
            secondsUntilAutoClaimAvailable = fud_info[7];

            fud_balance = fud_balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateHedgeFundInfo({
                    chain: 'avax',
                    contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33', //fud
                    info: {
                        balance,
                        withdrawableDividends,
                        totalDividends,
                        secondsUntilAutoClaimAvailable
                    }
                }
            ))

            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33', //fud
                balance: fud_balance
            }))

            // HEDGE TOKEN
            withdrawableDividends = hedge_info[3];
            totalDividends = hedge_info[4];
            secondsUntilAutoClaimAvailable = hedge_info[7];

            hedge_balance = hedge_balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateWalletHEDGEInfo({
                balance: hedge_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateHedgeFundInfo({
                    chain: 'avax',
                    contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a', //hedge
                    info: {
                        balance,
                        withdrawableDividends,
                        totalDividends,
                        secondsUntilAutoClaimAvailable
                    }
                }
            ))

            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a', //hedge
                balance: hedge_balance
            }))

            dispatch(updateHedgeFundBalances({
                chain: 'avax',
                contract_address: '0x726573a7774317DD108ACcb2720Ac9848581F01D', //cheems
                balance: cheems_balance / (10 ** 18)
            }))

        })
            .catch(console.log)
    }

    const getUserInfo = () => {

        //AVAX
        const web3AVAX = new Web3(rpc.avax);
        web3AVAX.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateBalances({
                chain: 'avax',
                contract_address: 'native',
                balance: resp / (10 ** 18)
            }))
        })

        const fook_contract = new web3AVAX.eth.Contract(coineus_abi, '0x95923F63dA09B4f7520f7C65a31F318D8228B744');
        const fud_contract = new web3AVAX.eth.Contract(coineus_abi, '0x80363cc2CAa2545554d155031e47049ee2F98f33');
        const usdc_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E');
        const wavax_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7');
        const hedge_contract = new web3AVAX.eth.Contract(coineus_abi, '0xe387cF241B6685D405C575ce35db78ed4844B34a');

        const callsAVAX = [
            fook_contract.methods.balanceOf(wallet.address).call,
            fook_contract.methods.getAccountDividendsInfo(wallet.address).call,
            usdc_contract.methods.balanceOf(wallet.address).call,
            fud_contract.methods.balanceOf(wallet.address).call,
            fud_contract.methods.getAccountDividendsInfo(wallet.address).call,
            wavax_contract.methods.balanceOf(wallet.address).call,
            hedge_contract.methods.balanceOf(wallet.address).call,
            hedge_contract.methods.getAccountDividendsInfo(wallet.address).call,
        ];

        makeBatchRequest(web3AVAX, callsAVAX).then(resp => {
            let [
                balance,
                info,
                usdc_balance,
                fud_balance,
                fud_info,
                wavax_balance,
                hedge_balance,
                hedge_info,
            ] = resp;

            let withdrawableDividends = info[3];
            let totalDividends = info[4];
            let secondsUntilAutoClaimAvailable = info[7];

            balance = balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 6);
            totalDividends = totalDividends / (10 ** 6);

            dispatch(updateWalletFOOKInfo({
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateBalances({
                chain: 'avax',
                contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744', //fook
                balance: balance
            }))

            dispatch(updateBalances({
                chain: 'avax',
                contract_address: '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E', //usdc
                balance: usdc_balance / (10 ** 6)
            }))

            dispatch(updateBalances({
                chain: 'avax',
                contract_address: '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7', //wavax
                balance: wavax_balance / (10 ** 18)
            }))

            // FUD TOKEN
            withdrawableDividends = fud_info[3];
            totalDividends = fud_info[4];
            secondsUntilAutoClaimAvailable = fud_info[7];

            fud_balance = fud_balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateWalletFUDInfo({
                balance: fud_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateBalances({
                chain: 'avax',
                contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33', //fud
                balance: fud_balance
            }))

            // HEDGE TOKEN
            withdrawableDividends = hedge_info[3];
            totalDividends = hedge_info[4];
            secondsUntilAutoClaimAvailable = hedge_info[7];

            hedge_balance = hedge_balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateWalletHEDGEInfo({
                balance: hedge_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateBalances({
                chain: 'avax',
                contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a', //hedge
                balance: hedge_balance
            }))

        })
            .catch(console.log)
    }

    const claimHedge = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, '0xe387cF241B6685D405C575ce35db78ed4844B34a');

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserInfo();
                    setPending(false);
                });
        }
    }

    if (!Object.entries(info).length || btc.data === undefined) {
        return <></>;
    }

    return (<Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <HomePagePriceWidget
                            avatar="https://coineus.app/assets/tokens/avax/0xe387cF241B6685D405C575ce35db78ed4844B34a/logo.png"
                            price={prices.hedge?.inUSD}
                        />
                        <HomePagePriceWidget
                            avatar="https://coineus.app/assets/tokens/avax/logo.png"
                            price={prices.avax?.inUSD}
                        />
                    </Grid>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{border: '1px solid #00BBc5', padding: 10, backgroundColor: '#000'}}>
                        <ListItem>
                            <ListItemText
                                primary={<>
                                    {info.priceChange.h24 >= 0 ? <ArrowCircleUpIcon style={{color: '#00ff00', marginRight: 5, verticalAlign: 'sub'}}/> :
                                    <ArrowCircleDownIcon style={{color: '#ff0000', marginRight: 5, verticalAlign: 'sub'}}/>}
                                    <Typography fontSize={20} style={{display:"inline-block"}} fontWeight={700}>{`${info.priceChange.h24}%`}</Typography>
                                </>}
                                secondary="24HR CHANGE"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(info.volume.h24)}
                                primaryTypographyProps={{fontSize: 20, fontWeight: 700}}
                                secondary="24HR VOLUME"
                            />
                        </ListItem>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{border: '1px solid #00BBc5', padding: 10, backgroundColor: '#000'}}>
                        <ListItem>
                            <ListItemText
                                primary={CoineusUSDFormat(info.liquidity.usd)}
                                primaryTypographyProps={{fontSize: 20, fontWeight: 700}}
                                secondary="TOTAL LIQUIDITY USD"
                            />
                        </ListItem>
                        <ListItem>

                            <ListItemAvatar style={{minWidth:30}}>
                                <Avatar
                                    src="https://coineus.app/assets/tokens/avax/0xe387cF241B6685D405C575ce35db78ed4844B34a/logo.png"
                                    sx={{width: 24, height: 24}}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary={CoineusCryptoFormat(info.liquidity.base)}
                            />

                            <ListItemAvatar style={{minWidth:30}}>
                                <Avatar src="https://coineus.app/assets/tokens/avax/logo.png"
                                        sx={{width: 24, height: 24}}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary={CoineusCryptoFormat(info.liquidity.quote)}
                            />
                        </ListItem>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper style={{border: '1px solid #00BBc5', padding: 10, backgroundColor: '#000'}}>
                        <ListItem>
                            <ListItemText
                                primary={`${CoineusCryptoFormat(totalD)} FOOK`}
                                secondary="Total Rewards Distributed All-time"
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700, fontSize: '2.125rem'}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Divider style={{marginBottom: 10}}>MY WALLET</Divider>
                    {
                        wallet.address ?
                            <TokenListItem
                                contract_address="0xe387cF241B6685D405C575ce35db78ed4844B34a"
                                name="HEDGE"
                                network="avax"
                                price={prices.hedge}
                                wallet={wallet}
                                rewards={{
                                    allTime: wallet.hedgeInfo?.totalDividends,
                                    pending: wallet.hedgeInfo?.withdrawableDividends,
                                    symbol: 'FOOK'
                                }}
                                rewardsClaim={
                                    <ListItem style={{color: '#00BBC5'}}>
                                        <Button onClick={claimHedge} variant='contained' fullWidth
                                                disabled={
                                                    wallet.chain !== 43114 ||
                                                    wallet.hedgeInfo?.withdrawableDividends === 0 ||
                                                    !wallet.address ||
                                                    wallet.hedgeInfo?.secondsUntilAutoClaimAvailable > 0
                                                }>{wallet.chain !== 43114 ? 'Change to AVAX Chain' : `Claim ${wallet.hedgeInfo?.secondsUntilAutoClaimAvailable > 0 ? 'Wait' : ''}`}</Button>
                                    </ListItem>
                                }
                            />
                            :
                            <Paper style={{border: '1px solid #00BBc5', padding: 10, backgroundColor: '#000'}}>
                                <ListItem>
                                    <ListItemText
                                        primary="CONNECT WALLET"
                                        primaryTypographyProps={{textAlign: 'center', fontSize: 20, color: 'primary'}}
                                    />
                                </ListItem>
                            </Paper>
                    }
                </Grid>
                <Grid item xs={12}>
                    <Divider style={{marginBottom: 10}}>HEDGE FUND HOLDINGS</Divider>
                    <Grid item xs={12}>
                        <Paper style={{color: '#fff', backgroundColor: '#00BBC5'}}>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusUSDFormat(fundTotal)}
                                    secondary="Fund Total Value"
                                    primaryTypographyProps={{textAlign: 'right', fontWeight: 700, fontSize: '2.125rem'}}
                                    secondaryTypographyProps={{textAlign: 'right', color: '#fff'}}
                                />
                            </ListItem>
                        </Paper>
                    </Grid>
                    <TokenListItem
                        contract_address="native"
                        name="AVAX"
                        network="avax"
                        price={prices.avax}
                        wallet={marketingWallet}
                    />
                    <TokenListItem
                        contract_address="0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E"
                        name="USDC"
                        network="avax"
                        price={{inUSD: 1}}
                        wallet={marketingWallet}
                    />
                    <TokenListItem
                        contract_address="0xe387cF241B6685D405C575ce35db78ed4844B34a"
                        name="HEDGE"
                        network="avax"
                        price={prices.hedge}
                        wallet={marketingWallet}
                        rewards={{
                            allTime: marketingWallet.info.avax["0xe387cF241B6685D405C575ce35db78ed4844B34a"]?.totalDividends,
                            pending: marketingWallet.info.avax["0xe387cF241B6685D405C575ce35db78ed4844B34a"]?.withdrawableDividends,
                            symbol: 'FOOK'
                        }}
                    />
                    <TokenListItem
                        contract_address="0x95923F63dA09B4f7520f7C65a31F318D8228B744"
                        name="FOOK"
                        network="avax"
                        price={prices.fook}
                        wallet={marketingWallet}
                        rewards={{
                            allTime: marketingWallet.info.avax["0x95923F63dA09B4f7520f7C65a31F318D8228B744"]?.totalDividends,
                            pending: marketingWallet.info.avax["0x95923F63dA09B4f7520f7C65a31F318D8228B744"]?.withdrawableDividends,
                            symbol: 'USDC'
                        }}
                    />
                    <TokenListItem
                        contract_address="0x726573a7774317DD108ACcb2720Ac9848581F01D"
                        name="CheemsX"
                        network="avax"
                        price={prices.cheems}
                        wallet={marketingWallet}
                    />
                </Grid>
            </Grid>

            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Container>
    );
}


function HomePagePriceWidget(props) {

    const {avatar, price} = props;

    return <Grid item xs={6}>
        <Paper style={{border: '1px solid #00BBc5', padding: 10, backgroundColor: '#000'}}>
            <ListItem>
                <ListItemAvatar>
                    <Avatar src={avatar}/>
                </ListItemAvatar>
                <ListItemText
                    primary={CoineusUSDFormat(price)}
                    primaryTypographyProps={{textAlign: 'right', fontSize: 20, fontWeight: 700}}
                />
            </ListItem>
        </Paper>
    </Grid>
}