import store from "../redux/store";
import Web3 from "web3";
import unipair_abi from "../abis/IUniswapV2Pair.json";
import {makeBatchRequest} from "./promisify";
import {updatePrices} from "../redux/slices/coineusSlice";

export default function getPairPrices() {

    const _store = store.getState();

    const {prices, rpc} = _store.coineus;

    //AVAX
    const web3AVAX = new Web3(rpc.avax);
    const usdc_avax = new web3AVAX.eth.Contract(unipair_abi, prices.USDCAVAXpair);
    const fook_avax = new web3AVAX.eth.Contract(unipair_abi, prices.FOOKAVAXpair);
    const fud_avax = new web3AVAX.eth.Contract(unipair_abi, prices.FUDAVAXpair);
    const hedge_avax = new web3AVAX.eth.Contract(unipair_abi, prices.HEDGEAVAXpair);
    const cheems_avax = new web3AVAX.eth.Contract(unipair_abi, prices.CHEEMSAVAXpair);

    const callsAVAX = [
        usdc_avax.methods.getReserves().call,
        fook_avax.methods.getReserves().call,
        fud_avax.methods.getReserves().call,
        hedge_avax.methods.getReserves().call,
        cheems_avax.methods.getReserves().call
    ]

    makeBatchRequest(web3AVAX, callsAVAX)
        .then(resp => {
            const [
                usdc_avax_reserves,
                fook_avax_reserves,
                fud_avax_reserves,
                hedge_avax_reserves,
                cheems_avax_reserves
            ] = resp

            const avax_price = usdc_avax_reserves.reserve1/usdc_avax_reserves.reserve0 * (10**12)

            store.dispatch(updatePrices({
                name: 'avax',
                chain: 'avax',
                contract_address: 'native',
                price: {
                    inUSD: avax_price,
                    inAVAX: 1
                }
            }));

            store.dispatch(updatePrices({
                name: 'usdc',
                chain: 'avax',
                contract_address: '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E',
                price: {
                    inUSD: 1,
                    inAVAX: usdc_avax_reserves.reserve0/usdc_avax_reserves.reserve1 / (10**12)
                }
            }));

            store.dispatch(updatePrices({
                name: 'fook',
                chain: 'avax',
                contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744',
                price: {
                    inAVAX: fook_avax_reserves.reserve1/fook_avax_reserves.reserve0,
                    inUSD: fook_avax_reserves.reserve1/fook_avax_reserves.reserve0 * avax_price
                }
            }));

            store.dispatch(updatePrices({
                name: 'fud',
                chain: 'avax',
                contract_address: '0x80363cc2CAa2545554d155031e47049ee2F98f33',
                price: {
                    inAVAX: fud_avax_reserves.reserve1/fud_avax_reserves.reserve0,
                    inUSD: fud_avax_reserves.reserve1/fud_avax_reserves.reserve0 * avax_price
                }
            }));

            store.dispatch(updatePrices({
                name: 'hedge',
                chain: 'avax',
                contract_address: '0xe387cF241B6685D405C575ce35db78ed4844B34a',
                price: {
                    inAVAX: hedge_avax_reserves.reserve0/hedge_avax_reserves.reserve1,
                    inUSD: hedge_avax_reserves.reserve0/hedge_avax_reserves.reserve1 * avax_price
                }
            }));

            store.dispatch(updatePrices({
                name: 'cheems',
                chain: 'avax',
                contract_address: '0x726573a7774317DD108ACcb2720Ac9848581F01D',
                price: {
                    inAVAX: cheems_avax_reserves.reserve1/cheems_avax_reserves.reserve0,
                    inUSD: cheems_avax_reserves.reserve1/cheems_avax_reserves.reserve0 * avax_price
                }
            }));

        });


}